const NextGameCountdown = () => {
  const navigate = useNavigate();
  const [timeRemaining, setTimeRemaining] = useState({ days: 0, hours: 0, minutes: 0 });
  const [nextGame, setNextGame] = useState(null);

  useEffect(() => {
    const findNextGame = () => {
      const now = new Date();
      return ALL_GAMES.find(game => {
        const gameDateTime = new Date(`${game.date} ${game.time}`);
        return gameDateTime > now && !game.result;
      });
    };

    const game = findNextGame();
    setNextGame(game);

    const calculateTimeRemaining = () => {
      if (!game) return;

      const gameTime = new Date(`${game.date} ${game.time}`);
      const now = new Date();
      const diff = gameTime - now;

      if (diff > 0) {
        const days = Math.floor(diff / (1000 * 60 * 60 * 24));
        const hours = Math.floor((diff % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
        const minutes = Math.floor((diff % (1000 * 60 * 60)) / (1000 * 60));

        setTimeRemaining({ days, hours, minutes });
      }
    };

    calculateTimeRemaining();
    const timer = setInterval(calculateTimeRemaining, 60000);

    return () => clearInterval(timer);
  }, []);

  if (!nextGame) return null;

  if (!nextGame) return null;

  return (
    <div 
      onClick={() => navigate('/schedule')}
      className="flex items-center space-x-2 bg-gray-50 rounded-full px-3 py-2 text-sm 
                 cursor-pointer transition-all duration-300 hover:scale-105 
                 hover:bg-gray-100 active:scale-95 hover:shadow-md"
    >
      <Clock className="w-4 h-4 text-[#003057] shrink-0" />
      <div className="hidden sm:block">
        <span className="font-medium text-[#003057]">Next:</span>
        <span className="ml-2">{nextGame.opponent}</span>
        <span className="mx-1"></span>
        <span className="text-gray-600">{nextGame.location}</span>
      </div>
      <div className="sm:hidden">
        <span className="font-medium text-[#003057]">{nextGame.opponent}</span>
      </div>
      <div className="font-medium shrink-0">
        <span className="bg-[#003057] text-white px-2 py-1 rounded-full text-xs sm:text-sm whitespace-nowrap">
          {timeRemaining.days}d {timeRemaining.hours}h
        </span>
      </div>
    </div>
  );
};

